@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* import text-input color variables */

:root {
  --color-background-text-input-default: #ffffff;
  --color-background-text-input-disabled: #e5e5e5;
  --color-border-text-input-default: #808080;
  --color-border-text-input-hover: #1a1a1a;
  --color-border-text-input-focus: #1a1a1a;
  --color-border-text-input-invalid: #b01038;
  --color-border-text-input-disabled: #e5e5e5;
  --color-focus-outline-text-input-default: #0072c6;
  --color-helper-text-input-default: #666666; /* input helper text color */
  --color-helper-text-input-invalid: #b01038; /* invalid input helper text color */
  --color-icon-text-input-invalid: #b01038;
  --color-input-text-input-default: #1a1a1a; /* input field text color */
  --color-input-text-input-disabled: #999898; /* disabled input field text color */
  --color-label-text-input-default: #1a1a1a; /* input label text color */
  --color-label-text-input-invalid: #1a1a1a; /* invalid input label text color */
  --color-placeholder-text-input-default: #666666;
}

.hds-text-input {
  --border-radius: 2px;
  --border-width: 2px;
  --outline-width: 3px;
  --input-height: 56px;
  --textarea-height: 149px;
  --icon-size: 1.375em;
  --label-height: 1.625rem;

  --helper-color-default: var(--color-helper-text-input-default);
  --helper-color-invalid: var(--color-helper-text-input-invalid);
  --icon-color-invalid: var(--color-icon-text-input-invalid);
  --input-background-default: var(--color-background-text-input-default);
  --input-background-disabled: var(--color-background-text-input-disabled);
  --input-border-color-default: var(--color-border-text-input-default);
  --input-border-color-hover : var(--color-border-text-input-hover);
  --input-border-color-focus: var(--color-border-text-input-focus);
  --input-border-color-invalid: var(--color-border-text-input-invalid);
  --input-border-color-disabled: var(--color-border-text-input-disabled);
  --input-color-default: var(--color-input-text-input-default);
  --input-color-disabled: var(--color-input-text-input-disabled);
  --label-color-default: var(--color-label-text-input-default);
  --label-color-invalid: var(--color-label-text-input-invalid);
  --placeholder-color: var(--color-placeholder-text-input-default);
  --focus-outline-color: var(--color-focus-outline-text-input-default);
}

/* text-input transitions */

.hds-text-input .hds-text-input__input:hover {
  transition: border-color 85ms ease-out;
}

.hds-text-input .hds-text-input__input:not([readonly]):focus {
  transition: 85ms ease-out;
  transition-property: box-shadow, transform;
}

.hds-text-input--invalid .hds-text-input__label {
  transition: color 85ms linear;
}

/* LABEL */

.hds-text-input__label {
  color: var(--label-color-default);
  display: block;
  font-size: 0.875em;
  font-weight: bold;
  line-height: var(--label-height);
}

/* INPUT */

.hds-text-input__input-wrapper {
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.hds-text-input .hds-text-input__input {
  background-color: var(--input-background-default);
  border: var(--border-width) solid var(--input-border-color-default);
  border-radius: var(--border-radius);
  color: var(--input-color-default);
  font-size: 1.125em;
  height: var(--input-height);
  line-height: normal;
  padding: var(--spacing-s);
  width: 100%;
}

.hds-text-input textarea.hds-text-input__input {
  height: var(--textarea-height);
  min-height: var(--input-height);
  resize: vertical;
}

.hds-text-input .hds-text-input__input:hover {
  border-color: var(--input-border-color-hover);
}

.hds-text-input .hds-text-input__input:focus {
  border-color: var(--input-border-color-focus);
  outline: none;
}

.hds-text-input .hds-text-input__input:-ms-input-placeholder {
  color: var(--placeholder-color);
}

.hds-text-input .hds-text-input__input::placeholder {
  color: var(--placeholder-color);
}

/* FOCUS OUTLINE */

.hds-text-input .hds-text-input__input:not([readonly]):focus {
  box-shadow: 0 0 0 var(--outline-width) var(--focus-outline-color);
  transform: translate3d(0, 0, 0);
}

/* HELPER TEXT */

.hds-text-input__helper-text {
  color: var(--helper-color-default);
  display: block;
  line-height: var(--label-height);
  font-size: 0.875em;
}

/* INVALID */

.hds-text-input--invalid .hds-text-input__label {
  color: var(--label-color-invalid);
}

.hds-text-input--invalid .hds-text-input__helper-text {
  color: var(--helper-color-invalid);
}

.hds-text-input.hds-text-input--invalid .hds-text-input__input {
  border-color: var(--input-border-color-invalid);
  padding-right: calc(var(--icon-size) + var(--spacing-l));
}

/* INVALID ICON */

.hds-text-input.hds-text-input--invalid .hds-text-input__input-wrapper::after {
  animation: fadeIn 85ms ease-out;
  background: var(--icon-color-invalid);
  content: '';
  height: var(--icon-size);
  mask-image: url("data:image/svg+xml;charset=utf-8,%3C?xml version='1.0' encoding='UTF-8'?%3E %3Csvg xmlns='http://www.w3.org/2000/svg' width='100%25' height='100%25' viewBox='0 0 23 23'%3E %3Cpath fillRule='evenodd' d='M11.5 23C5.148 23 0 17.852 0 11.5 0 5.15 5.148 0 11.5 0 17.85 0 23 5.15 23 11.5 23 17.852 17.85 23 11.5 23zm0-2.464c4.981 0 9.035-4.054 9.035-9.036 0-4.982-4.054-9.036-9.035-9.036S2.465 6.518 2.465 11.5c0 4.982 4.054 9.036 9.035 9.036zm1.232-13.184h-2.464V4.97h2.464v2.381zm0 9.916h-2.464V9.484h2.464v7.783z'/%3E %3C/svg%3E");
  -webkit-mask-image: url("data:image/svg+xml;charset=utf-8,%3C?xml version='1.0' encoding='UTF-8'?%3E %3Csvg xmlns='http://www.w3.org/2000/svg' width='100%25' height='100%25' viewBox='0 0 23 23'%3E %3Cpath fillRule='evenodd' d='M11.5 23C5.148 23 0 17.852 0 11.5 0 5.15 5.148 0 11.5 0 17.85 0 23 5.15 23 11.5 23 17.852 17.85 23 11.5 23zm0-2.464c4.981 0 9.035-4.054 9.035-9.036 0-4.982-4.054-9.036-9.035-9.036S2.465 6.518 2.465 11.5c0 4.982 4.054 9.036 9.035 9.036zm1.232-13.184h-2.464V4.97h2.464v2.381zm0 9.916h-2.464V9.484h2.464v7.783z'/%3E %3C/svg%3E");
  position: absolute;
  right: var(--border-width);
  top: 0;
  -ms-transform: translate(calc(var(--spacing-s) * -1), calc(var(--input-height) / 2 - 50%));
      transform: translate(calc(var(--spacing-s) * -1), calc(var(--input-height) / 2 - 50%));
  width: var(--icon-size);
}

/* DISABLED */

.hds-text-input .hds-text-input__input[disabled] {
  background-color: var(--input-background-disabled);
  border-color: var(--input-border-color-disabled);
  color: var(--input-color-disabled);
  cursor: not-allowed;
}

/* READ-ONLY */

.hds-text-input .hds-text-input__input[readonly] {
  background-color: transparent;
  border: 0;
  color: var(--input-color-default);
  padding: 0;
  /* fixes faded text color on Safari */
  -webkit-text-fill-color: var(--input-color-default);
}
